<template>
  <div>
    <div class="panel panel-primary">
      <div class="panel-heading">
        <h3 class="panel-title">Add Songs</h3>
      </div>
      <div class="panel-body form-inline">
        <label>
          Song:
          <input
            type="text"
            class="form-control"
            v-model="trackName"
            @keyup.enter="addToTable"
          />
        </label>

        <input
          type="button"
          value="add"
          class="btn btn-primary"
          @click="addToTable"
        />
      </div>
      <div class="panel-body form-inline">
        <label>
          Search:
          <input type="text" class="form-control" v-model="keyword" v-focus />
        </label>
      </div>
    </div>

    <div class="table-wrapper">
      <div style="text-align: right; padding-right: 10px">
        <a
          href=""
          class="glyphicon glyphicon-repeat"
          aria-hidden="true"
          @click.prevent="getAll"
        >
          Refresh
        </a>
      </div>
      <table class="table table-bordered table-hover table-striped">
        <thead>
          <tr>
            <th>ID</th>
            <th>Track Name</th>
            <th>Operation</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in search()" :key="item.id">
            <td v-text="item.id"></td>
            <td>{{ item.trackName }}</td>

            <td>
              <a href="" @click.prevent="del(item.id)">Delete</a>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>


<script>
export default {
  name: "Admin",
  data() {
    return {
      list: [],
      trackName: "",
      keyword: "",
    };
  },
  methods: {
    addToTable() {
      if (this.trackName == "") {
        this.$message({
          showClose: true,
          message: 'Song cannot be blank',
          type: 'error'
        });
        return;
      }
      this.$http.post(
        process.env.BASE_URL + "db/admin/songInsertion",
        JSON.stringify({ TrackName: this.trackName }),
        { emulateJSON: true }
      ).catch((err) => {
        this.$message({
          showClose: true,
          message: 'Song name you entered is either already in the database or invalid',
          type: 'error'
        });
      })
      this.id = "";
      this.trackName = "";
    },

    //to be implemented
    del(id) {
      this.list.forEach((element, index) => {
        if (element.id == id) {
          this.list.splice(index, 1);
        }
      });
      this.$http.delete(process.env.BASE_URL + "db/admin/" + id)
    },

    search() {
      var myList = [];
      this.list.forEach((element) => {
        if (element.trackName.includes(this.keyword)) {
          myList.push(element);
        }
      });
      return myList;
    },
    //to be fixed
    getAll() {
      this.$http.get(process.env.BASE_URL + "db/admin").then((res) => {
        this.list = res.body;
      });
    },
  },
  directives: {
    focus: {
      //for each of these function, the first para is el (a DOM object)
      bind: function (el) {
        //when directive bind with the element, excute this function ONLY ONCE
        el.focus();
      },
      inserted: function (el) {
        //when the element inserted into DOM, excute this function ONLY ONCE
        el.focus();
      },
      updated: function () {
        //when VNode updated, excute this function, may excute a few times
      },
    },
  },
  created() {
    this.getAll();
  },
};
</script>

<style scoped>
.panel {
  margin-right: 10%;
  margin-left: 10%;
}
.table-wrapper {
  margin-right: 10%;
  margin-left: 10%;
  margin-top: 15px;
}
</style>